.App {
  text-align: center;
  background-image: url('./blue-1845901_1920.jpg');  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Glass {
  min-height: 60vh;
  min-width: 80vw;
  margin: 15vh;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.815); 
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  backdrop-filter: blur(5px); 
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
